body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}


h1{
    padding: 1rem 0;
    text-align: center;
}
nav{
    text-align: center;
    width: 100%;
    background: black;
    margin-bottom:4rem;
}
nav a{
    display: inline-block;
    padding: 1rem 2rem;
    text-decoration: none;
    background-color: black;
    color: white;
}
nav a:hover{
    background-color: #9161d2;
    color: white;
}
ul{
    padding: 0 5%;
}
li{
    border:0.5px solid #9161d2;
    list-style: none;
    border-left: .5rem solid #9161d2;
    margin: 2rem 0;
    padding: 2rem;
    line-height: 2rem;
}
button{
    padding: .5rem 1rem;
    float: right;
    margin-right: 1rem;
    background-color: white;
    border: 1px solid #9161d2;
}
button a{
    font-size: 1rem;
    text-decoration: none;
    color: #9161d2;
}
.loader{
    width: 100%;
    text-align: center;
    font-size: 3rem;
}
a.active{
    background-color: #9161d2;
}
@-webkit-keyframes loader {
    from {font-size: 1rem; color: white;}
    to {font-size: 6rem; color:black;}
}
@keyframes loader {
    from {font-size: 1rem; color: white;}
    to {font-size: 6rem; color:black;}
}
.loader{
    font-weight: 700;
    -webkit-animation-name: loader;
            animation-name: loader;
    -webkit-animation-duration: 0.1s;
            animation-duration: 0.1s;
}
